body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-image:url('img/bg1.jpg'); 
}

ol, ul {
  padding-left: 30px;
}

.card {
  border:1px solid #999;
  float:left;
  border-radius:10%;
  height:100px;
  width:60px;
  padding:20px;
  margin:20px;
  color:white;
}

.card img {
  padding:0px;
  margin:0px;
  border-radius:10%;
  width:60px;
  height:100px;
}

.hidden {
  color:blue;
  border:solid 1px white;
  padding:0px;
  margin:10px;
}

.hidden:hover {
  color:pink;
}

.flipped {
  border:solid 1px white;
  padding:0;
  margin:10px;
}

.matched {
  -webkit-transition-property: border;
  -o-transition-property: border;
  transition-property: border;
  -webkit-transition-duration: 2s;
       -o-transition-duration: 2s;
          transition-duration: 2s;
  -webkit-transition-delay: 0s;
       -o-transition-delay: 0s;
          transition-delay: 0s;
  border: solid 2px yellow; 
  padding:0;
  margin:10px;
}

.game {
  width:75%;
  margin: auto;
  padding-top:5%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}


.board-row:after {
  clear: both;
  content: "";
  display: table;
}



